
import React,{useState} from 'react';
import QRCode from 'qrcode.react';

function App() {


  const [ProductID1,setProductID1] = useState('');
  const [ProductID2,setProductID2] = useState('');
  const [ProductID3,setProductID3] = useState('');
  const [ProductIDescription,setProductIDescription] = useState('');
  const [WareHouse,setWareHouse] = useState('');
  const [Manufacturer,setManufacturer] = useState('');
  const [qrCodeText,setQRCodeText] = useState('');
 
//console.log(inputText);
  const generateQRCode = () => {

   /* inputText=JSON.stringify({
      ProductID :ProductID1 +'-' +ProductID2 + '-' +ProductID3,
      ProductIDescription : '\n'+ ProductIDescription,    
      WareHouse :'\n'+ WareHouse,
      Manufacturer :Manufacturer});
      */
   
    var   ProductID = 'ProductID:' + ProductID1 +'-' +ProductID2 + '-' +ProductID3;
    var inputText = ProductID +"\n\n";
    inputText += 'ProductIDescription:' + ProductIDescription +"\n\n";
    inputText += 'WareHouse:' + WareHouse +"\n\n";
    inputText += 'Manufacturer:' + Manufacturer +"\n";
    
    setQRCodeText(inputText);
  }

  const downloadQRCode = () =>{
    const qrCodeURL = document.getElementById('qrCodeEl')
     .toDataURL("image/png")
     .replace("image/png","image/octet-stream");
     console.log(qrCodeURL);
     let aEl = document.createElement("a");
     aEl.href =qrCodeURL;
     aEl.download ="QR_Code.png";
     document.body.appendChild(aEl);
     aEl.click();
     document.body.removeChild(aEl);

  }
  return (
    <div className="App">
     <h3 style={{textAlign: "center"}}> Generate and download a QR code image in React  </h3>
    
    <div className="qr-input" style={{textAlign: "center", padding: 10}}>

     
     
      <input
      type ="text"
      placeholder ="ProductID1"
      value ={ProductID1}
      onChange ={e=>setProductID1(e.target.value)}
      />
      <br/>

      <input
      type ="text"
      placeholder ="ProductID2"
      value ={ProductID2}
      onChange ={e=>setProductID2(e.target.value)}
      />
      <br/>

      <input
      type ="text"
      placeholder ="ProductID3"
      value ={ProductID3}
      onChange ={e=>setProductID3(e.target.value)}
      />
      <br/>

      <input
      type ="text"
      placeholder ="ProductIDescription"
      value ={ProductIDescription}
      onChange ={e=>setProductIDescription(e.target.value)}
      />
      <br/>

      <input
      type ="text"
      placeholder ="WareHouse"
      value ={WareHouse}
      onChange ={e=>setWareHouse(e.target.value)}
      />
      <br/>



      <input
      type ="text"
      placeholder ="Manufacturer"
      value ={Manufacturer}
      onChange ={e=>setManufacturer(e.target.value)}
      />
      <br/>
     
      <input style={{margin: 10}}
        type ="button"
        value ="Generate"
        onClick ={generateQRCode}

       />
               <br/>
       <QRCode
       id ="qrCodeEl"
       size ={150}
       value ={qrCodeText}
        />
        <br/>

        <input style={{margin: 10}}
        type ="button"
        className = "download-btn"
        value ="Download"               
        onClick = {downloadQRCode}
        />

    </div>
    </div>
  );
}

export default App;
